import React from 'react';
import cx from 'classnames';

import A from 'components/a';

import css from './home.module.css';

export default () => (
  <div className={cx('container', css.home)}>
    <ul className={css.info}>
      <li className={css.greetings}>HELLO WORLD!</li>
      <li className={css.name}>
        I AM <span>MARLO</span>
      </li>
      <li className={css['level-1']}>Software Developer</li>
      <li className={css['level-2']}>Meme Evangelist</li>
      <li className={css['level-3']}>Gamer</li>
    </ul>

    <ul className={css.socials}>
      <li className={css.gmail}>
        <A href='mailto:marlodelatorre11@gmail.com' />
      </li>
      <li className={css.linkedin}>
        <A href='https://www.linkedin.com/in/marlo-dela-torre-b98389102/' />
      </li>
      <li className={css.github}>
        <A href='https://github.com/marloeleven' />
      </li>
      <li className={css.facebook}>
        <A href='https://www.facebook.com/marloeleven' />
      </li>
    </ul>
  </div>
);
